.paint__information-container-list {
  color: #596A62;
  font-weight: 500;
  text-decoration: none;
  width: 90vw;
}

.paint__title-list {
  display: flex;
  align-items: flex-start;
  align-content: flex-start;
}

.paint__title-list__element {
  display: inline;
  text-align: left;
  margin: 0px;
  padding: 0px;
  margin-bottom: 3%;
}

.paint__title-list__number {
  width: 10%;
}

.paint__title-list__name {
  width: 60%;
  margin-block-start: 0px !important;
  margin-block-end: 0px !important;
}

.paint__title-list__date {
  width: 10%;
  margin: 0px;
  padding: 0px;
}

.paint__title-list__measures {
  width: 20%;
  margin: 0px;
  padding: 0px;
}

.paint__title-list__price {
  width: 10%;
  margin: 0px;
  padding: 0px;
}

@media (min-width: 319px) and (max-width: 374px) {
  .paint__information-container-list {
    width: 90vw;
  }

  .paint__title-list__element {
    font-size: 8px;
  }

  .paint__title-list__date {
    font-size: 6px;
  }

  .paint__title-list__measures {
    font-size: 7px;
  }
}

@media (min-width: 375px) and (max-width: 481px) {
  .paint__information-container-list {
    width: 90vw;
  }

  .paint__title-list__element {
    font-size: 9px;
  }

  .paint__title-list__date {
    font-size: 7px;
  }

  .paint__title-list__measures {
    font-size: 8px;
  }
}

@media (min-width: 481px) and (max-width: 768px) {
  .paint__information-container-list {
    width: 70vw;
  }

  .paint__title-list__element {
    font-size: 14px;
  }

  .paint__title-list__date {
    font-size: 12px;
  }

  .paint__title-list__measures {
    font-size: 13px;
  }
}

@media (min-width: 769px) and (max-width: 1024px) {
  .paint__information-container-list {
    width: 60vw;
  }

  .paint__title-list__element {
    font-size: 16px;
  }

  .paint__title-list__date {
    font-size: 14px;
  }

  .paint__title-list__measures {
    font-size: 15px;
  }
}

@media (min-width: 1025px) {
  .paint__information-container-list {
    width: 60vw;
  }

  .paint__title-list__element {
    font-size: 20px;
  }

  .paint__title-list__date {
    font-size: 18px;
  }

  .paint__title-list__measures {
    font-size: 19px;
  }
}
